import React, { useState, useRef, useEffect } from 'react';
import styled from 'styled-components';
import { Container, TextField, IconButton, Dialog, DialogActions, DialogContent, DialogTitle } from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import { keyframes } from 'styled-components';

const float = keyframes`
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
`;

const fadeIn = keyframes`
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
`;

const PageContainer = styled.div`
  min-height: 100vh;
  background: linear-gradient(135deg, #f8f5ff 0%, #fff 100%);
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23a78bfa' fill-opacity='0.1'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  }
`;

const ChatContainer = styled(Container)`
  display: flex;
  flex-direction: column;
  height: calc(100vh - 70px);
  margin-top: 70px;
  padding: 2rem;
  max-width: 1000px !important;
  position: relative;
  z-index: 1;

  @media (max-width: 768px) {
    padding: 1rem;
  }
`;

const HeaderContainer = styled.div`
  position: relative;
  width: 100%;
  padding: 0 1rem;
  margin-bottom: 1rem;
`;

const Title = styled.h1`
  text-align: center;
  color: #333;
  font-size: 1.8rem;
  margin: 1rem 0;
  font-weight: 600;

  span {
    background: linear-gradient(45deg, #8B5CF6, #A78BFA);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
`;

const MessagesContainer = styled.div`
  flex: 1;
  overflow-y: auto;
  margin-bottom: 2rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  border-radius: 1.5rem;
  box-shadow: 0 8px 32px rgba(167, 139, 250, 0.1);

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 3px;
  }

  &::-webkit-scrollbar-thumb {
    background: #a78bfa;
    border-radius: 3px;
    
    &:hover {
      background: #9461fb;
    }
  }
`;

const MessageBubble = styled.div`
  display: flex;
  margin-bottom: 1.5rem;
  animation: ${fadeIn} 0.3s ease-out;
  align-items: flex-start;
`;

const userGradient = 'linear-gradient(45deg, #8B5CF6, #A78BFA)';

const Avatar = styled.div`
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: ${props => props.isUser ? '0 0 0 1rem' : '0 1rem 0 0'};
  background: ${props => props.isUser ? userGradient : '#fff'};
  border: 2px solid ${props => props.isUser ? 'transparent' : '#a78bfa'};
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  color: ${props => props.isUser ? 'white' : '#a78bfa'};
  box-shadow: 0 4px 12px rgba(167, 139, 250, 0.15);
  animation: ${float} 3s ease-in-out infinite;
`;

const MessageContent = styled.div`
  background: ${props => props.isUser ? userGradient : '#fff'};
  color: ${props => props.isUser ? 'white' : '#333'};
  padding: 0.75rem 1rem;
  border-radius: 1.2rem;
  border: 1px solid ${props => props.isUser ? 'transparent' : 'rgba(167, 139, 250, 0.2)'};
  max-width: 70%;
  font-size: 0.95rem;
  line-height: 1.6;
  position: relative;
  box-shadow: 0 4px 12px rgba(167, 139, 250, 0.1);
  white-space: pre-line;

  &::before {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border: 8px solid transparent;
    ${props => props.isUser ? `
      border-left-color: #8B5CF6;
      right: -16px;
    ` : `
      border-right-color: #fff;
      left: -16px;
    `}
    top: 12px;
  }
`;

const InputContainer = styled.div`
  display: flex;
  gap: 1rem;
  align-items: center;
  padding: 1.2rem;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  border-radius: 1.2rem;
  box-shadow: 0 8px 32px rgba(167, 139, 250, 0.1);
  border: 1px solid rgba(167, 139, 250, 0.2);
`;

const StyledTextField = styled(TextField)`
  .MuiOutlinedInput-root {
    border-radius: 1rem;
    background: rgba(255, 255, 255, 0.9);
    transition: all 0.3s ease;
    
    &:hover {
      background: #fff;
    }
    
    &:hover .MuiOutlinedInput-notchedOutline {
      border-color: #a78bfa;
    }
    
    &.Mui-focused {
      background: #fff;
      box-shadow: 0 0 0 2px rgba(167, 139, 250, 0.2);
      
      .MuiOutlinedInput-notchedOutline {
        border-color: #a78bfa;
      }
    }
  }
`;

const SendButton = styled(IconButton)`
  &.MuiIconButton-root {
    background: linear-gradient(45deg, #6B46C1, #9F7AEA);
    color: white;
    padding: 0.8rem;
    border-radius: 1rem;
    transition: all 0.3s ease;
    
    &:hover {
      transform: translateY(-2px);
      box-shadow: 0 4px 12px rgba(167, 139, 250, 0.3);
    }
    
    &:active {
      transform: translateY(0);
    }
    
    &.Mui-disabled {
      background: #e2e8f0;
      color: #94a3b8;
    }
  }
`;

const ClearButton = styled(IconButton)`
  && {
    color: #8B5CF6;
    opacity: 0.7;
    transition: all 0.2s;
    padding: 12px;
    position: absolute;
    right: 1rem;
    top: 50%;
    transform: translateY(-50%);
    
    &:hover {
      opacity: 1;
      background: rgba(139, 92, 246, 0.1);

      &::after {
        content: '';
        position: absolute;
        bottom: -20px;
        left: 50%;
        transform: translateX(-50%);
        background: rgba(0, 0, 0, 0.7);
        color: white;
        padding: 4px 8px;
        border-radius: 4px;
        font-size: 12px;
        white-space: nowrap;
      }
    }
  }
`;

const StyledDialog = styled(Dialog)`
  && .MuiDialog-paper {
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(10px);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(139, 92, 246, 0.1);
  }
`;

const DialogButton = styled.button`
  padding: 8px 24px;
  border-radius: 12px;
  font-size: 0.9rem;
  font-weight: 500;
  border: none;
  cursor: pointer;
  transition: all 0.2s;
  margin: 0 8px;
  
  ${props => props.cancel && `
    background: #f3f4f6;
    color: #666;
    
    &:hover {
      background: #e5e7eb;
    }
  `}
  
  ${props => props.confirm && `
    background: #8B5CF6;
    color: white;
    
    &:hover {
      background: #7c3aed;
    }
  `}
`;

const Chat = () => {
  const [messages, setMessages] = useState(() => {
    const savedMessages = localStorage.getItem('chatHistory');
    return savedMessages ? JSON.parse(savedMessages) : [{
      content: "AIMomo",
      fullContent: "AIMomo",
      isUser: false
    }];
  });
  
  const [openDialog, setOpenDialog] = useState(false);
  
  useEffect(() => {
    localStorage.setItem('chatHistory', JSON.stringify(messages));
  }, [messages]);

  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [currentTypingIndex, setCurrentTypingIndex] = useState(-1);
  const messagesEndRef = useRef(null);

  const clearHistory = () => {
    setOpenDialog(false);
    const initialMessage = {
      content: "AIMomo",
      fullContent: "AIMomo",
      isUser: false
    };
    setMessages([initialMessage]);
    localStorage.setItem('chatHistory', JSON.stringify([initialMessage]));
  };

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  useEffect(() => {
    if (currentTypingIndex >= 0 && currentTypingIndex < messages.length) {
      const message = messages[currentTypingIndex];
      if (message.content !== message.fullContent) {
        const timer = setTimeout(() => {
          setMessages(prevMessages => {
            const newMessages = [...prevMessages];
            const currentMessage = newMessages[currentTypingIndex];
            const nextChar = currentMessage.fullContent[currentMessage.content.length];
            if (nextChar) {
              currentMessage.content += nextChar;
            }
            return newMessages;
          });
        }, 20); 
        return () => clearTimeout(timer);
      } else {
        setCurrentTypingIndex(-1);
      }
    }
  }, [messages, currentTypingIndex]);

  const formatResponse = (text) => {
    return text
      .replace(/\*\*/g, '')
      .replace(/\\n\\n/g, '\n')  
      .replace(/\\n/g, '\n');    
  };

  const handleSend = async () => {
    if (!input.trim()) return;

    const userMessage = { 
      content: input, 
      fullContent: input,
      isUser: true 
    };
    setMessages(prev => [...prev, userMessage]);
    setInput('');
    setIsLoading(true);

    try {
      const recentMessages = messages.slice(-4); 

      const currentDate = new Date().toLocaleString('zh-CN', { 
        timeZone: 'America/New_York',
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        hour12: false
      });

      const response = await fetch('https://api.deepseek.com/v1/chat/completions', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${process.env.REACT_APP_DEEPSEEK_API_KEY}`
        },
        body: JSON.stringify({
          model: "deepseek-chat",
          messages: [
            {
              role: "system",
              content: `Rachel WangAIMomo Markdown ****
              
              ${currentDate}
              
              
              
              - UX| GPA: 3.93 08/2023 - 12/2025  : :Shell  Git
              - (with Merit)    09/2022 - 08/2023
              -    09/2019 - 06/2022
              - xuechun@umich.edu
              - (+1)7348828356 / (+86)18009699186 (EST, -12h)
              - 111
              - 
              - mbti: enfp
              
              
              - PRD
              - FigmaPhotoshopAdobe XDAdobe IllustratorUnreal EngineBlender
              - ReactDjangoHTML/CSSJavaScriptPythonRJavaSQL
              - Jira
              
              
              1.  - 2024.08 - 2024.12
              - RPACVPRD
              - RPA
              - 1024RPA
              
              2.  - 2021.04 - 2021.12
              - 
              - 
              - 
              
              3.  - 2020.06 - 2020.08
              -  ,

              
              1. Use Your Voice| 01/2025 - 04/2025
              - 7
              - MVP
              - sprintJira
              - ReactDjango
              
              2. ParkEase APP| 09/2023 - 12/2023
              - 
              - Adobe XDUI
              
              3. StoryScape App| 09/2023 - 12/2023
              - 0-10APP
              - Figma

              4. 3D () | 05/2023 - 08/2023
              -  
              -  Blenderunreal5
              -  Wordpress3D
            
              5.  () 06/2022 - 08/2022
              -  Twitter
              -  

              6.  APP | | 02/2020 - 05/2020
              -  APPGoogle Maps
              -  
              -  
            
              
              - 
              - 
              - 
              - 
              
              `
            },
            // 
            ...recentMessages.map(msg => ({
              role: msg.isUser ? "user" : "assistant",
              content: msg.fullContent
            })),
            {
              role: "user",
              content: input
            }
          ],
          temperature: 0.8,
          max_tokens: 2000
        })
      });

      if (!response.ok) {
        throw new Error('API request failed');
      }

      const data = await response.json();
      const formattedContent = formatResponse(data.choices[0].message.content);
      const aiMessage = {
        content: '',
        fullContent: formattedContent,
        isUser: false
      };
      
      setMessages(prev => [...prev, aiMessage]);
      setCurrentTypingIndex(messages.length + 1);
    } catch (error) {
      console.error('Error:', error);
      const errorMessage = {
        content: '',
        fullContent: "",
        isUser: false
      };
      setMessages(prev => [...prev, errorMessage]);
      setCurrentTypingIndex(messages.length + 1);
    } finally {
      setIsLoading(false);
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSend();
    }
  };

  return (
    <PageContainer>
      <ChatContainer>
        <HeaderContainer>
          <Title>Chat with <span>Rachel</span></Title>
          <ClearButton onClick={() => setOpenDialog(true)}>
            <DeleteOutlineIcon sx={{ fontSize: 28 }} />
          </ClearButton>
        </HeaderContainer>
        <MessagesContainer>
          {messages.map((message, index) => (
            <MessageBubble key={index} style={{ justifyContent: message.isUser ? 'flex-end' : 'flex-start' }}>
              {!message.isUser && <Avatar isUser={false}>R</Avatar>}
              <MessageContent isUser={message.isUser}>
                {message.content}
              </MessageContent>
              {message.isUser && <Avatar isUser={true}></Avatar>}
            </MessageBubble>
          ))}
          <div ref={messagesEndRef} />
        </MessagesContainer>
        <InputContainer>
          <StyledTextField
            fullWidth
            multiline
            maxRows={4}
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyPress={handleKeyPress}
            placeholder="..."
            disabled={isLoading}
          />
          <SendButton onClick={handleSend} disabled={isLoading || !input.trim()}>
            <SendIcon />
          </SendButton>
        </InputContainer>
      </ChatContainer>

      <StyledDialog
        open={openDialog}
        onClose={() => setOpenDialog(false)}
        PaperProps={{
          style: {
            borderRadius: '16px',
            padding: '8px'
          }
        }}
      >
        <DialogTitle sx={{
          fontFamily: "'SF Pro Display', sans-serif",
          fontSize: '1.2rem',
          color: '#333',
          textAlign: 'center'
        }}>
          
        </DialogTitle>
        <DialogContent sx={{
          textAlign: 'center',
          color: '#666',
          paddingBottom: '24px'
        }}>
          
        </DialogContent>
        <DialogActions sx={{ padding: '0 24px 16px', justifyContent: 'center' }}>
          <DialogButton onClick={() => setOpenDialog(false)} cancel>
            
          </DialogButton>
          <DialogButton onClick={clearHistory} confirm>
            
          </DialogButton>
        </DialogActions>
      </StyledDialog>
    </PageContainer>
  );
};

export default Chat;
